.body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  .body{
    font-family: "Verdana", cursive, sans-serif;
    color: #ffffff;
  }
  body{
    margin: 0;
    padding: 0;
    user-select: none;
  }
  .body {
    /* background-color: #7cc6fe; */
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 50vh;
  }
  
  .app {
    background-color: #252d4a;
    width: 450px;
    min-height: 200px;
    height: min-content;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
    display: flex;
    justify-content: space-evenly;
  }
  
  .scoreSection {
    display: flex;
    font-size: 24px;
    align-items: center;
  }
  
  /* QUESTION/TIMER/LEFT SECTION */
  .questionSection {
    width: 100%;
    position: relative;
  }
  
  .questionCount {
    margin-bottom: 20px;
  }
  
  .questionCount span {
    font-size: 28px;
  }
  
  .questionText {
    margin-bottom: 12px;
  }
  
  /* .timer-text {
    background: rgb(230, 153, 12);
    padding: 15px;
    margin-top: 20px;
    margin-right: 20px;
    border: 5px solid rgb(255, 189, 67);
    border-radius: 15px;
    text-align: center;
  } */
  
  /* ANSWERS/RIGHT SECTION */
  .answerSection {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .germanTestsButton {
    width: 100%;
    font-size: 16px;
    color: #ffffff;
    background-color: #252d4a;
    border-radius: 15px;
    display: flex;
    padding: 5px;
    justify-content: flex-start;
    align-items: center;
    border: 5px solid #234668;
    cursor: pointer;
  }
  
  /* .correct {
    background-color: #2f922f;
  }
  
  .incorrect {
    background-color: #ff3333;
  } */
  
  .germanTestsButton:hover {
    background-color: #555e7d;
  }
  
  .germanTestsButton:focus {
    outline: none;
  }
  
  .germanTestsButton svg {
    margin-right: 5px;
  }